<template>
  <!-- <div id="home">
    <ul>
       <li><router-link to="/notePage" >进入博客</router-link></li>
    </ul>
  </div> -->
  <div id="home" class="home-wrap">
    <div class="content">
      <router-link to="/notePage">
      <div class="img-wrap">
        <img
          src="../../static/logo.png"
          class="avatar"
          url="/notePage"
        />
      </div>
      </router-link>
      <div class="desc">守护微笑！</div>
      <router-link to="/notePage">
        <el-button plain style="opacity:0.4;color:#000">进入博客</el-button>
      </router-link>
      <!-- <m-audio src="~@/assets/唯你一生.mp3"></m-audio> -->
        <el-button plain style="opacity:0.4;color:#000" ref="play" @click="play">播放音乐</el-button>
        <audio ref="audio1" loop="loop" autoplay="autoplay">
            <source src="../../static/kuuga.mp3">
        </audio>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  components: {
  },
  methods: {
    play(){
      console.log('播放起来')
        this.$refs.audio1.play();//调用play（）方法播放
    }
  },
  data () {
    return {
      query: '',
    }
  },
  mounted() {
    this.$refs.play.$el.click()
  }
};
</script>

<style scoped>
.home-wrap {
  height: 100vh;
  width: 100vw;
  background: url("../../static/back.jpg");
  background-size: cover;
}
.content {
  position: absolute;
  top: 200px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.img-wrap {
  width: 128px;
  height: 128px;
  margin-bottom: 50px;
  
}
.desc {
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 30px;
  line-height: 65px;
  color: #fff;
  font-family: "Nunito", sans-serif;
}
.avatar {
    width: 100%;
    height: 100%;
    border: 4px solid #fff;
    border-radius: 50%;
  }
</style>



